.product-container {
  display: flex;
  flex-wrap: wrap;
  width: 1000px;
  margin: auto;
}

.product-left, .product-right {
  padding: 0 2rem;
  min-height: 400px;
  width: 50%;
  box-sizing: border-box;
  text-align: left;
}

.product-image-container {
  position: relative;
}

.product-curve {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
}

.product-background-name {
  font-size: 320px;
  position: fixed;
  left: -40px;
  top: 400px;
  z-index: -1;
  opacity: 0.2;
  white-space: nowrap;
}

.product-title {
  font-size: 6rem;
  margin: 0 0 48px;
  white-space: nowrap;
}

.product-sub-header {
  font-size: 2.5rem;
  margin: 0 0 48px;
  white-space: nowrap;
}